import { useState, useEffect } from 'react';
let gloabalId = 0;

const useGlobalId = (id, componentName) => {
  const [resultId, setResultId] = useState(id);
  useEffect(() => {
    if (!resultId) {
      setResultId(`module-${componentName}-${gloabalId}`);
      gloabalId += 1;
    }
  }, []);
  return resultId;
};

export default useGlobalId;