const range = (start, end) => {
  const length = end - start;
  return Array.from({
    length
  }, (_, i) => start + i);
};

export const ELLIPSIS = '...';

const usePagination = (current, pageCount, siblingCount) => {
  const startPage = 1;
  const endPage = pageCount;
  const siblingsStart = Math.max(Math.min(current - siblingCount, pageCount - siblingCount * 2 - 2), 3);
  const siblingsEnd = Math.min(Math.max(current + siblingCount, siblingCount * 2 + 3), endPage - 2);
  const itemList = [startPage, ...(siblingsStart > 3 ? [ELLIPSIS] : pageCount - 1 <= 2 ? [] : [2]), ...range(siblingsStart, siblingsEnd + 1), ...(siblingsEnd < pageCount - 2 ? [ELLIPSIS] : pageCount - 1 <= 1 ? [] : [pageCount - 1]), ...(startPage === endPage ? [] : [endPage])];
  return itemList.map((item, index) => ({
    item: item.toString(),
    key: `${item}-${index}`
  }));
};

export default usePagination;